* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 20px;
  background-color: #282c34;
  font-family: "Karla", sans-serif;
}

main {
  background-color: #f5f5f5;
  height: 400px;
  max-width: 800px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
}
.die-front {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: white;
}

.die-num {
  font-size: 1.7rem;
}

.rolling {
  border: none;
  font-family: "Karla", sans-serif;
  height: 50px;
  width: 150px;
  background-color: rebeccapurple;
  color: #f5f5f5;
  font-weight: 300;
  font-size: 1.5rem;
  border-radius: 6px;
  cursor: pointer;
}

.rolling:focus {
  outline: none;
}

.rolling:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
